.Home-Page {
  text-align: center;
  font-family: Inter, Arial, Helvetica, sans-serif;
  display: grid;
  grid-template-columns: repeat(12, [col-start] 1fr);
  gap: 20px;
}
.Home-Page > * {
  grid-column: col-start / span 12;
}

#logo {
  max-width: 75%;
  height: auto;
}

#calculate-button {
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  align-items: center;
  border-radius: 10px;
  width: 345px;
  height: 71px;
}

@media (min-width: 700px) {
  .Home-Text {
    grid-column: col-start 2 / span 4;
    grid-row: 2;
    font-weight: 900;
    font-size: 58px;
    line-height: 70px;
    text-align: left;
  }
  .Home-Image {
    grid-column: col-start 6 / span 5;
    grid-row: 2;
  }
}
